<template>
  <AppTabs>
    <AppTab title="Order history">
      <AppLoader v-if="loading" :is-dark="false" />

      <OrderHistory v-else-if="error == ''" :orders="orders" />
      <article v-else>
        <div class="user-order-history__error">
          <p>There war an error {{ error }}</p>
        </div>
      </article>
    </AppTab>
    <AppTab title="Change Password">
      <ChangePassword @change-password="changePassword"></ChangePassword>
    </AppTab>
  </AppTabs>
</template>
<script setup>
import { onMounted } from "vue";
import { createNamespacedHelpers } from "vuex-composition-helpers";

import {
  GET_ORDERS_ACTION,
  ORDERS,
  ERROR,
  LOADING,
  CHANGE_PASSWORD_ACTION,
} from "@/store/storeConstants";

import AppLoader from "../components/general/AppLoader.vue";
import OrderHistory from "../components/OrderHistory.vue";
import ChangePassword from "../components/ChangePassword.vue";
import AppTab from "../components/general/AppTab.vue";
import AppTabs from "../components/general/AppTabs.vue";
const { useGetters, useActions } = createNamespacedHelpers("orders");
const { useActions: useAuthActions } = createNamespacedHelpers("auth");
const {
  [ORDERS]: orders,
  [ERROR]: error,
  [LOADING]: loading,
} = useGetters([ORDERS, ERROR, LOADING]);
const { [GET_ORDERS_ACTION]: getOrders } = useActions([GET_ORDERS_ACTION]);
const { [CHANGE_PASSWORD_ACTION]: changePassword } = useAuthActions([
  CHANGE_PASSWORD_ACTION,
]);

onMounted(async () => {
  await getOrders();
});
</script>
<style lang="scss">
.user-order-history {
  &__title {
    padding-bottom: 20px;
    font-size: 1em;
    font-weight: 600;
    text-align: left;
  }
  &__order-list {
    list-style: none;
    padding-left: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;

    &-item {
      grid-template-columns: 1fr;
      grid-template-rows: 20px 20px 20px 1fr;
      justify-items: center;
      display: grid;
      background: $secondary-light-color;
      text-align: left;
      gap: 0;
      padding: 10px 10px;
      box-shadow: 0px 3px 3px 0px rgba($secondary-dark-color, 0.17);
      border: 1px solid rgba($secondary-dark-color, 0.17);
      border-radius: 5px;
      &-date {
        //   grid-area: date;
        font-size: 14px;

        &-label {
          font-weight: 500;
          color: $secondary-dark-color;
        }
      }
      &-num {
        font-size: 14px;
        // grid-area: num;
        &-label {
          font-weight: bold;
          color: $secondary-dark-color;
        }
      }
      &-price {
        font-size: 14px;
        padding-bottom: 5px;
        //  grid-area: price;
        &-label {
          font-weight: 500;
          color: $secondary-dark-color;
        }
      }

      &-meals-table {
        border-top: 1px dashed $secondary-dark-color;
        display: grid;
        padding-top: 5px;
        grid-template-columns: 5fr 1fr 1fr;
        font-size: 12px;
        gap: 5px;
        &-td {
          &--header {
            font-weight: bold;
          }
          &--right {
            text-align: right;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: $mediaExtraLarge) {
  .user-order-history__order-list {
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
}
@media only screen and (max-width: $mediaSmallScreen) {
  .user-order-history__order-list {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
}
@media only screen and (max-width: $mediaMobile) {
  .user-order-history__order-list {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}
</style>
